.header-class {
    position: fixed; /* 헤더를 화면 상단에 고정시킴 */
    top: 0; /* 화면 위쪽에 고정시킴 */
    width: 100%; /* 화면 너비에 맞춤 */
    background-color: #ffffff; /* 헤더 배경색 지정 */
    color: #070707; /* 헤더 글자색 지정 */
    padding: 10px; /* 헤더 패딩 설정 (선택사항) */
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1); /* 그림자 효과 (선택사항) */
    z-index: 1000; /* 다른 요소 위에 헤더를 배치 (선택사항) */
  }
.logo-class:hover{
  cursor: pointer;
}
.account-class { 
  float: right;
  cursor: pointer;
  border: 1px solid #fff;
  transition: background-color 0.3s;
  }
.account-class:hover {
  background-color: #333;
  color: #fff;
}


/* Modal styles */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background-color: #fff;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  text-align: center;
  width:30%;
}

.modal h2 {
  margin: 0;
}

.modal input {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
}

.modal button {
  padding: 10px 20px;
  margin: 10px;
  cursor: pointer;
  border: none;
  background-color: #007bff;
  color: #fff;
  border-radius: 5px;
}

.modal button:hover {
  background-color: #0056b3;
}

.modal button:last-child {
  background-color: #ccc;
  color: #333;
}

.modal button:last-child:hover {
  background-color: #999;
}

/* 화면 크기가 768px 이하일 때 스타일 변경 (모바일)*/

@media (max-width: 768px) {
  h1{
    font-size: 20px;
  }
  .modal-content {
    width: 80vw;
  }
  .header-class {
    transition: height 0.3s; /* 높이 변경에 대한 애니메이션 설정 */
  }
  .menu-open {
    height: 250px; /* 메뉴가 열린 상태의 헤더 높이 */
  }
  .menu-list {
    display: none;
  }
  
  .menu-open .menu-list {
    display: block; /* 메뉴가 열린 상태일 때 메뉴 리스트를 보여줍니다. */
  }
  .li-parent{

    list-style:decimal;
  }
  .li-submenu {
    list-style: none; /* 리스트 마커 제거 */
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 18px;
    padding: 0;
  }
}
/* 화면 크기가 768px 이상일 때 스타일 변경 (웹)*/
@media (min-width: 768px) {
  .fa-bars{
    display: none;
  }
  .menu-list {
    display: none;
  }
}

