/* Navigation.css */
.left-nav {
  background-color: #ffffff; /* 배경색을 흰색으로 설정 */
  color: #333; /* 글자색 설정 */
  padding: 20px; /* 패딩 설정 */
  width: 200px; /* 네비게이션 너비 설정 */
  }
  
  .left-nav ul {
    list-style: none; /* 리스트 마커 제거 */
    padding: 0;
  }
  
  .left-nav li {
    margin-bottom: 10px; /* 각 항목 사이 여백 설정 */
  }
  
  .left-nav a {
    color: #333; /* 링크 글자색 설정 */
    text-decoration: none; /* 밑줄 제거 */
    font-size: 18px; /* 글자 크기 설정 */
  }

  .profile-img {
    width: 200px; /* 원하는 너비로 조절 */
    height: 200px; /* 원하는 높이로 조절 */
  }

  .li-sub{
    font-weight: bold;
  }

  /* 화면 크기가 768px 이하일 때 스타일 변경 */
@media (max-width: 768px) {
  .left-nav {
    display: none;
    background-color: #ffffff; /* 배경색 설정 */
    padding: 5px; /* 패딩 설정 */
    width:25vw;
  }

  .profile-img {
    width: 70px; /* 원하는 너비로 조절 */
    height: 70px; /* 원하는 높이로 조절 */
  }
  .li-sub{
    font-weight: bold;
  }
  .left-nav a {
    color: #000000; /* 링크 글자색 설정 */
    text-decoration: none; /* 밑줄 제거 */
    font-size: 14px; /* 글자 크기 설정 */
  }

}